body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#searchContainer, #resultsContainer {
  color: white;
  background-color: #708090;
  padding: 2vw;
  margin-top:20px;
  font-size: 3vw;
}


#resultsContainer {
  font-size: 1rem;
}

#resultsContainer h3 {
  margin-bottom: 3.5vw;
}

.bookResults {
  background-color: white;
  color: black;
  padding: 1.75vw;
  font-size: 1rem;
}

.bookResults:not(:last-of-type) {
  margin-bottom: 2vw;
}

.bookResults h4 {
  font-size: 1.25rem;
  margin-bottom: 1vw;
}

.bookResults .row:first-of-type {
  margin-bottom: 1vw;
}

.bookResult img {
  display: inline-block;
  box-sizing: border-box;
  max-width: 100px;
  max-height: auto;
  margin-right: 2vw;
}

.aboutBook {
  box-sizing: border-box;
  display: inline-block;
  width: 75%;
  padding: 0;
}

.divButton {
  box-sizing: border-box;
  float: right;
}

.divButton button:first-of-type {
  margin-right: 1.75vw;
}

.jumbotron {
  background-image: url(/static/media/background.78bb60d2.jpg);
  background-size: cover;
}

.jumbotron h1 {
	color:white;
	text-shadow: 2px 2px black;
}

.jumbotron p {
	color:white;
	text-shadow: 2px 2px black;
	font-weight: bold;
}
